    padding: 11.5px 37px;
    font-size: 16px;
    font-weight: 700;
    line-height: 1.31;
    display: block;
  }
}

.header__button--show {
  cursor: pointer;
  color: #d41443;
  min-width: 210px;
  border: none;
  border-radius: 22px;
  margin: 0 auto;
